import { Col, Row } from "antd";
import React from "react";
import { useTranslation } from "react-i18next";
import Slider from "react-slick";
import styled from "styled-components";
import seafood from "../../../assets/images/seafood.png";
import slide1 from "../../../assets/images/slide1.png";
import slide2 from "../../../assets/images/slide2.png";
import slide3 from "../../../assets/images/slide3.png";
import tuong from "../../../assets/images/tuong.png";
import turtle from "../../../assets/images/turtle.jpg";

const IntroCarousel = () => {
  const { t } = useTranslation();

  const settings = {
    dots: true,
    arrow: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    // prevArrow: <PrevArrow />,
    // nextArrow: <NextArrow />,
  };

  return (
    <div>
      <div className="titleHolder">
        <h2>{t("about.title")}</h2>
        <p>{t("about.desc")}</p>
      </div>
      <div className="contentHolder">
        <CarouselWrapper {...settings} autoplay>
          <div>
            <Row gutter={[16, 16]} style={{ display: "flex" }}>
              <Col md={{ span: 12 }}>
                <img src={slide1} alt="natural" width={"100%"} />
              </Col>
              <Col md={{ span: 12 }}>
                <h2>{t("about.naturalTitle")}</h2>
                <p style={{ textAlign: "justify" }}>{t("about.naturalDesc")}</p>
                <h2> {t("about.placeTitle")}</h2>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <ul
                    style={{
                      textAlign: "left",
                    }}
                  >
                    <li>Bi m Tru</li>
                    <li>Bi Nht</li>
                    <li>Hn By Cnh</li>
                    <li>Vnh m Tre</li>
                    <li>Hn Tre Nh</li>
                    <li>Hn Tre Ln</li>
                    <li>ng Ty Bc</li>
                  </ul>
                  <img src={turtle} alt="traveler1" width={"60%"} />
                </div>
              </Col>
            </Row>
          </div>
          <div>
            <Row gutter={[16, 16]} style={{ display: "flex" }}>
              <Col md={{ span: 12 }}>
                <img src={slide3} alt="natural" width={"100%"} />
              </Col>
              <Col md={{ span: 12 }}>
                <h2>{t("about.historyTitle")}</h2>
                <p style={{ textAlign: "justify" }}>{t("about.historyDesc")}</p>
                <h2> {t("about.placeTitle")}</h2>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <ul style={{ textAlign: "left" }}>
                    <li>Nh t Cn o</li>
                    <li>Ngha trang Hng Dng</li>
                    <li>Cha Ni Mt(Vn Sn T)</li>
                    <li>Bo Tng Cn o</li>
                    <li>Cu Tu 914</li>
                    <li>Miu B Phi Yn</li>
                    <li>Cu Ma Thin Lnh</li>
                    <li>Nh Cha o</li>
                  </ul>
                  <img
                    src={tuong}
                    alt="tuong"
                    width={"35%"}
                    style={{
                      opacity: 0.8,
                    }}
                  />
                </div>
              </Col>
            </Row>
          </div>
          <div>
            <Row gutter={[16, 16]} style={{ display: "flex" }}>
              <Col md={{ span: 12 }}>
                <img src={slide2} alt="natural" width={"100%"} />
              </Col>
              <Col md={{ span: 12 }}>
                <h2> {t("about.specialtiesTitle")}</h2>
                <p style={{ textAlign: "justify" }}>
                  {t("about.specilitiesDesc")}
                </p>
                <h2> {t("about.foodTitle")}</h2>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <ul style={{ textAlign: "left" }}>
                    <li>Hi Sn Ti Cn o</li>
                    <li>Mt Ht Bng Cn o</li>
                    <li>Mm Hu Cn o</li>
                    <li>Mm Nhum Cn o</li>
                    <li>Cua Mt Trng Cn o</li>
                    <li>c V Nng Cn o</li>
                    <li>S Sng Cn o</li>
                    <li>C M  Cn o</li>
                    <li>Mc Mt Nng Cn o</li>
                    <li>C Thu Mt Nng Cn o</li>
                  </ul>
                  <img
                    src={seafood}
                    alt="seafood"
                    width={"50%"}
                    style={{ paddingTop: 24 }}
                  />
                </div>
              </Col>
            </Row>
          </div>
        </CarouselWrapper>
      </div>
    </div>
  );
};

export default IntroCarousel;

const CarouselWrapper = styled(Slider)`
  /* > .slick-dots li button {
    width: 16px;
    height: 4px;
    /* border-radius: 100%; */
  /* background: #8b8b8b; */
  /* } */
  /* > .slick-dots li.slick-active button { */
  /* width: 24px; */
  /* height: 4px; */
  /* border-radius: 100%; */
  /* background: #1890ff; */
  /* } */
`;
